import React, { useContext, useState, useEffect } from 'react'
import { Avatar, IconButton } from '@material-ui/core'
import { AttachFile, MoreVert, SearchOutlined } from '@material-ui/icons'
import InsertEmoticonIcon from '@material-ui/icons/InsertEmoticon'
import MicIcon from "@material-ui/icons/Mic"
import AuthContext from './context/AuthContext'

import Pusher from "pusher-js"
import axios from './axios'

import "./Chat.css"

function Chat(props) {
  let {user, logoutUser} = useContext(AuthContext)
  const [messages, setMessages] = useState("")

  const id = props.match.params.id
  const [userrs, setUsers] = useState("")


  let sendMessage = async (e)=> {
		
		e.preventDefault()

		let response = await fetch('https://belch.onrender.com/messages-create/', {

			method: 'POST',
      credentials: "include",
			headers:{
				'Content-Type':'application/json'
			},
			body:JSON.stringify({
      "message": e.target.message.value,
      "time": "2023-03-31T03:50:00Z",
      "sender": id,
      "receiver": user.user.pk

    
    })
		})
		
		let data = await response.json()
		
		console.log('response:', response)
		if (response.status === 200) {
			
			alert('Success!')
		} else{
			alert('something went wrong') 
		}
		
	}


  let getMessages = async ()=> {
    
    
    let response =  await fetch(`https://belch.onrender.com/messages/`, {
        method: "GET",
        credentials: "include",
        headers: {
       "Content-Type": "application/json"
  },
          
       
    })
   
    let data = await response.json()
    setMessages(data)
    console.log(data)
   

}






useEffect(() => {
 

const token = setInterval(getMessages, 5000) // Every 5 seconds?
getMessages(); // Initial request
return () => {
 // Don't forget to cleanup the interval when this effect is cleaned up.
 clearInterval(token)
}
  
  }, [])

  //console.log(messages)




  return (
    <div className="chat">
        <div className='chat_header'>
          <Avatar/>

          <div className='chat_headerInfo'>
            <h3>Chat</h3>
            
          </div>

          <div className='chat_headerRIght'>
            <IconButton>
              <SearchOutlined/>
            </IconButton>

            <IconButton>
              <AttachFile/>
            </IconButton>

            <IconButton>
              <MoreVert/>
            </IconButton>
          </div>
        </div>
        <div className='chat_body'>
  


{messages && messages.map((message, index)=>(
		<React.Fragment key = {index}>
      {message.sender == user.user.pk? 
    <p class="chat_message ">
    <span className='chat__name'></span>

    {message.message}
    <span className='chat_timestamp'>
      
    </span>
    
    </p>
    : null

    }
      
      {message.sender== id? 
      <p class="chat_message chat_receiver">
            <span className='chat__name'></span>

            {message.message}
            <span className='chat_timestamp'>
              
            </span>
            
            </p>
  
  : null

}
  


</React.Fragment>
		))}
            

              
        </div>
        <div className='chat_footer'>
          <InsertEmoticonIcon/>
          <form onSubmit={sendMessage}>
            <input name="message"
             placeholder="Type a message"
            type="text"
            />
            <input type="submit"/>
          </form>
          <MicIcon/>
        </div>
    </div>
  )
}

export default Chat